import _ from 'lodash'

export default {
  loaddb: (state, resdata) => {
    // console.log(resdata)
    // console.log(state['songs'])
    var songList = _.shuffle(resdata)
    state['songs'] = songList
  },
  clearQueueMut: (state) => {
    state['queue'] = []
  },
  changeCurrentMut: (state,song) => {
    state['currentSong'] = song
    // console.log(state['currentSong'])
  },
  addToQueueMut: (state, song) => {
    state['queue'].push(song)
  },
  clearCurrentMut: (state) => {
    state['currentSong'] = {}
  },
  nextSongMut: (state, index) => {
    state['currentSong'] = state['queue'][index]
  },
  preSongMut: (state,index) => {
    state['currentSong'] = state['queue'][index]
  },
  skipToSongMut: (state, index) => {
    state['currentSong'] = state['queue'][index]
  },
  removeFromQueueMut: (state, index) => {
    console.log(index)
    if(index > -1){
      state['queue'].slice(index, 1)
    }
  }
}
