import axios from 'axios'

const url = '/api/'

class getDB{
  static playlist(name){
    var uri = url + "play/" + name
    return new Promise((resolve, reject) => {
      axios.get(uri).then((res) => {
        const data = res.data[0].tracks
        // console.log(data)
        resolve(data)
      }).catch((err) => {
        console.log(err)
        reject(err)
      })
    })
  }
}

export default getDB
