<template>
<div class="homeMain">
  <carousel v-if="!isplayer" :togglePlayer="togglePlayer" />
  <Cards v-if="!isplayer" :togglePlayer="togglePlayer" />
  <player v-show="isplayer" />
</div>
</template>

<script>
// @ is an alias to /src
import carousel from '../components/carousel.vue'
import Cards from '../components/Cards.vue'
import player from '../components/player.vue'

import {
  mapState,
  mapGetters
} from 'vuex'

export default {
  name: 'Home',
  data() {
    return {
      isplayer: false,
      loaded: false
    }
  },
  components: {
    carousel,
    Cards,
    player,
  },
  methods: {
    togglePlayer() {
      this.isplayer = !this.isplayer
    },
    seek(e) {
      if (!this.loaded) return;

      const el = e.target.getBoundingClientRect();
      const seekPos = (e.clientX - el.left) / el.width;

      this.audio.currentTime = parseInt(this.audio.duration * seekPos);
    },
    stop() {
      this.playing = false;
      this.audio.currentTime = 0;
    },
    update(e) {
      // var biz = e
      // unused: true;
      this.currentSeconds = parseInt(this.audio.currentTime);
      return e;
    }
  },
  computed: {
    ...mapState(['songs']),
    ...mapGetters(['songsCount']),
    isLoaded() {
      return this.songsCount
    }

  },
  watch: {
    isLoaded: function() {
      this.loaded = true
    },
    songsCount: function() {
      console.log('change')
      this.loaded = true
    }
  }
}
</script>
<style lang="css" scoped>
.homeMain{
  margin-top: 64px !important;
}
</style>
