<template>
<div class="homeMain">
  <navbar />
  <transition name="slide-up">
    <carousel v-show="!isplayer" :togglePlayer="togglePlayer" />
  </transition>
  <transition name="slide-up">
    <Cards :playNow="playNow" :addToQueue="addToQueue" v-show="!isplayer" :togglePlayer="togglePlayer" />
  </transition>
  <transition name="slide-down-p">
    <player v-show="isplayer" :menuShow="menuShow" :toggleMenu="toggleMenu" :isplayer="isplayer" :togglePlayer="togglePlayer" :toggleQueue="toggleQueue" :preSong="preSong" :togglePlaying="togglePlaying" :nextSong="nextSong"
      :currentSeconds="currentSeconds" :durationSeconds="durationSeconds" :playing="playing" :seek="seek" :percentComplete="percentComplete" />
  </transition>
  <transition name="slide-down-bp">
    <bottomPlayer v-show="!isplayer" :playing="playing" :togglePlayer="togglePlayer" :togglePlaying='togglePlaying' :toggleQueue="toggleQueue" />
  </transition>

  <!-- <vue-page-transition name="fade-in-up">
    <router-view />
  </vue-page-transition> -->
  <!-- <div class="audioPlayer"> -->
  <!-- <audio id="audio" :loop="innerLoop" ref="audiofile" :src="'/api/stream/'+this.currentSong.code" preload="auto" style="display: none;"></audio> -->
  <!-- </div> -->
</div>
</template>

<script>
import navbar from './components/navbar.vue'
import carousel from './components/carousel.vue'
import Cards from './components/Cards.vue'
import player from './components/player.vue'
import bottomPlayer from './components/bottomPlayer.vue'

import {
  mapActions,
  mapState,
  mapGetters
} from 'vuex'

export default {
  name: 'App',
  data() {
    return {
      isplayer: false,
      currentSeconds: 0,
      durationSeconds: 0,
      innerLoop: false,
      loaded: false,
      autoPlay: false,
      loop: false,
      playing: false,
      audio: undefined,
      queueUpdate: false,
      currentIndex: 0,
      player: new Audio(),
      menuShow: false,

    }
  },
  components: {
    navbar,
    carousel,
    Cards,
    player,
    bottomPlayer,
  },
  methods: {
    logThis() {
      console.log(this.currentSong)
    },
    toggleMenu() {
      this.menuShow = !this.menuShow
    },
    toggleQueue() {
      this.togglePlayer()
      this.menuShow = !this.menuShow
    },
    ...mapActions(['loadDb', 'preSongAct', 'addToQueueAct', 'clearQueueAct', 'changeCurrentAct', 'clearCurrentAct', 'nextSongAct']),
    togglePlayer() {

      this.isplayer = !this.isplayer
    },
    playNow(song) {
      this.clearQueueAct()
      this.currentIndex = 0
      // console.log(song)
      this.changeCurrentAct(song)
      this.player.play()
      // this.playing = !this.playing
    },
    addToQueue(song) {
      if (!this.queueUpdate) {
        this.clearQueueAct()
        this.currentIndex = 0
        this.clearCurrentAct()
      }
      this.addToQueueAct(song)
      if (!this.queueUpdate) {
        this.changeCurrentAct(song)
        // setTimeout(this.togglePlaying(), 2000);
      }
      // this.playing = true;
      this.queueUpdate = true

    },
    nextSong() {
      if (this.currentIndex < (this.queue.length - 1)) {
        this.currentIndex++;
        this.nextSongAct(this.currentIndex)
        this.autoPlay = true
      } else {
        this.autoPlay = false
        // this.playing = !this.playing
      }
    },
    preSong() {
      var len = this.queue.length - 1
      if (this.currentSeconds > 0 || this.index === len) {
        this.player.load()
        this.player.play()
      } else {
        this.currentIndex--;
        this.preSongAct(this.currentIndex)
      }
    },
    togglePlaying() {
      this.playing = !this.playing
    },
    load() {
      this.loaded = true;
      this.durationSeconds = parseInt(this.player.duration);
      // console.log('loading...')
      // if (this.currentIndex < (this.queue.length - 1)) {
      //   this.autoPlay = true
      //   this.playing = true
      //   this.player.play()
      // }
      // return this.playing = this.autoPlay;
      // if (this.player.readyState >= 2) {}}

      // throw new Error('Failed to load sound file.');
    },
    seek(e) {
      if (!this.loaded) return;

      const el = e.target.getBoundingClientRect();
      const seekPos = (e.clientX - el.left) / el.width;

      this.player.currentTime = parseInt(this.player.duration * seekPos);
    },
    stop() {
      this.playing = false;
      this.player.currentTime = 0;
    },
    update(e) {
      // var biz = e
      // unused: true;
      this.currentSeconds = parseInt(this.player.currentTime);
      return e;
    }
  },
  computed: {
    ...mapState(['songs', 'currentSong', 'queue']),
    ...mapGetters(['songsCount']),
    isLoaded() {
      return this.songsCount
    },
    percentComplete() {
      return parseInt(this.currentSeconds / this.durationSeconds * 100);
    }
  },
  async created() {
    await this.loadDb();
    // this.player.src = '/api/stream/' + this.currentSong.code

  },
  watch: {
    currentSeconds(value) {
      if (value === this.durationSeconds) {
        this.nextSong()
      }
    },
    currentSong(value) {
      // this.load('second')
      // this.playing = true
      this.loaded = false
      if (typeof(this.currentSong.code) !== 'undefined') {
        this.player.src = '/api/stream/' + value.code
        this.player.play()
        document.title = 'TuneIn | ' + this.currentSong.name;
      }
      // var num = this.player.duration
      // console.log(this.player.duration)
      // if (!isNaN(num)) {
      //   console.log('here here')
      //   this.durationSeconds = parseInt(this.player.duration);
      // }
      // if (typeof(this.currentSong.code) !== 'undefined') {
      // }
    },
    playing(value) {
      if (value) {
        return this.player.play();
      }
      this.player.pause();
    },
    isLoaded: function() {
      this.loaded = true
    },
    songsCount: function() {
      console.log('change')
      this.loaded = true

    }
  },
  mounted() {
    this.clearCurrentAct()
    this.clearQueueAct()
    // this.audio = this.$el.querySelectorAll('audio')[0];
    this.player.addEventListener('timeupdate', this.update);
    this.player.addEventListener('loadeddata', this.load);
    this.player.addEventListener('pause', () => {
      this.playing = false;
    });
    this.player.addEventListener('play', () => {
      this.playing = true;
    });
  }
};
</script>


<style lang="css" scoped>
.homeMain{
  margin-top: 64px !important;
}

.slide-up-enter-active{
  transition: all 0.3s ease-in-out;
}
.slide-up-enter, .slide-up-leave-to{
  opacity: 0;
  transform: translateY(100vh);
}
.slide-down-p-enter-active{
  transition: all 0.3s ease-out;
}
.slide-down-p-enter, .slide-down-p-leave-to{
  opacity: 0;
  transform: translateY(-100vh);
}
.slide-down-bp-enter-active{
  transition: all 0.3s ease;
}
.slide-down-bp-enter, .slide-down-bp-leave-to{
  opacity: 0;
  transform: translateY(100vh);
}

</style>
