<template>
<div id="caroMain">
  <b-carousel id="carousel-1" :no-touch="false" :interval="4000" controls indicators background="#ababab" img-width="1024" img-height="480" style="text-shadow: 1px 1px 2px #333;">
    <!-- Text slides with image -->
    <div class="carouselFor" v-for="(song, index) in getPOP" :key="index">
      <b-carousel-slide img-height="480" img-width="1024" :caption="song.name" :text="song.artist">
        <template v-slot:img>
          <img class="d-block img-fluid w-100" width="1024" height="480" :src=" imageBase + song.code" alt="image">
        </template>
      </b-carousel-slide>
    </div>
  </b-carousel>
</div>
</template>

<script>
// import axios from 'axios';
import 'babel-polyfill'
import getDB from '../getDB'
import {
  mapState
} from 'vuex'

export default {
  props: ['togglePlayer'],
  data() {
    return {
      pop: [],
      imageBase: '/api/image/carousel/'
    }
  },
  methods: {
    filterMethod(song) {
      var status = this.pop.includes(song.code)
      return status
    },
    // redirectSong(code) {
    //
    // }
  },
  computed: {
    ...mapState(['songs']),
    getPOP() {

      var popList = this.songs.filter(this.filterMethod)
      return popList
    }
  },
  async created() {
    this.pop = await getDB.playlist('popular')
    if (screen.width <= 700) {
      this.imageBase = '/api/image/'
    }
  }

}
</script>

