<template>
<transition name="queue">
  <div id="queueMain">
    <div title="back" class="h1 exitButton" @click="toggleMenu">
      <b-icon icon="X"></b-icon>
    </div>
    <div class="container">
      <div class="row flexCenter text-center">
        <div @click="toggleQueue()" v-if="queue.length === 0" class="col-9 emptyQueue listElement">
          <div class="songTitle">
            No music in the queue. click to go check out some good songs.
          </div>
        </div>
        <div v-else v-for="(song, index) in queue" :key="index" class="col-12 col-md-9  listElement">
          <div :class="{ activePlay : song.code === currentSong.code, playedAlready : index < indexReturn, toBePlayed : index > indexReturn }" class="songTitle">
            {{ song.name }}
          </div>
          <div class="itemIcons">
            <span @click="removeFromQueueAct(index)" class="itemButtons h4">
              <b-icon icon="file-minus"></b-icon>
            </span>
            <span @click="skipToSongAct(index)" class="itemButtons h4">
              <b-icon icon="play"></b-icon>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>

</transition>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex'

export default {
  name: "queueMenu",
  props: ['toggleQueue', 'menuShow', 'toggleMenu', 'togglePlayer'],
  data() {
    return {

    }
  },
  computed: {
    ...mapState(['songs', 'currentSong', 'queue']),
    indexReturn() {
      var ind = this.queue.findIndex((song) => {
        return song.code === this.currentSong.code
      })
      return ind
    }
  },
  methods: {
    ...mapActions(['removeFromQueueAct', 'skipToSongAct']),
  }
}
</script>
