import axios from 'axios'

export default{
  loadDb: async ({ commit }) => {
    // var data = []
    await axios.get('/api/allSongs/').then(res => {
      const data = res.data
      commit('loaddb',data)
    }).catch(err => {
      console.log(err)
    })
    // console.log(resdata)
  },
  clearQueueAct: ({ commit }) => {
    commit('clearQueueMut')
  },
  changeCurrentAct: ({ commit }, song) => {
    commit('changeCurrentMut',song)
  },
  addToQueueAct: ({ commit }, song) => {
    commit('addToQueueMut', song)
  },
  clearCurrentAct: ({ commit }) => {
    commit('clearCurrentMut')
  },
  nextSongAct: ({ commit }, index) => {
    commit('nextSongMut', index)
  },
  preSongAct: ({ commit }, index) => {
    commit('preSongMut', index)
  },
  skipToSongAct: ({ commit },index) => {
    commit('skipToSongMut', index)
  },
  removeFromQueueAct: ({ commit }, index) => {
    commit('removeFromQueueMut', index)
  }
}
