<template>
<div id="bMMain">
  <div id="nowPlayingImg" @click="togglePlayer">
    <img v-if="currentSong.name" @click.prevent height="60" width="60" :src="'/api/image/'+currentSong.code" alt="cover image">
  </div>
  <div class="titleWrapper" @click="togglePlayer">
    <div class="nowPlayingTitle">
      {{ currentSong.name }}
    </div>
    <div class="nowPlayingArtist">
      {{ currentSong.artist }}
    </div>
  </div>
  <div v-b-hover="playIsHandle" @click="togglePlaying" :title="(playing) ? 'Pause' : 'Play'" class="h2 playIcon playNowButton">
    <b-icon v-if="playIsHovered && !playing" icon="play-fill"></b-icon>
    <b-icon v-if="!playIsHovered && !playing" icon="play"></b-icon>
    <b-icon v-if="playIsHovered && playing" icon="pause-fill"></b-icon>
    <b-icon v-if="!playIsHovered && playing" icon="pause"></b-icon>
  </div>
  <div class="h2 playNowButton queueIcon" @click="toggleQueue">
    <b-icon icon="text-indent-left"></b-icon>
  </div>
</div>
</template>
<script>
import {
  mapState
} from 'vuex'

export default {
  name: "bottomPlayer",
  props: ['togglePlaying', 'playing', 'togglePlayer', 'toggleQueue'],
  data: () => ({
    playIsHovered: false,
  }),
  computed: {
    ...mapState(['currentSong'])
  },
  methods: {
    playIsHandle(hovered) {
      this.playIsHovered = hovered
    },
  }
}
</script>
